// https://codepen.io/osublake/pen/ajKRWz

import './node_modules/raf-interval/raf-interval.js';
import { gsap, Draggable } from "gsap/all";
import {Howl} from 'howler';
gsap.registerPlugin(Draggable);


window.onload = function() {


  // Shuffle function
  function random(array) {
    return array[Math.floor(Math.random()*array.length)];
  }

  // Number functions
  function oneDecimal(n) {
    var number = n;
    var rounded = Math.round( number * 10 ) / 10;
    return rounded;
  }

  function toPercentage(x, n) {
    var p = 0;
    if (n) {
      p = ((x + 10) / 20) * 100;
    }
    else {
      p = ((x + 10) / 20);
    }
    return oneDecimal(p);
  }

  // Vibration
  function vibration(time) {
    if ("vibrate" in navigator) {
      window.navigator.vibrate(time);
    }      
  }

  
  // Top level variables
  var gameWindow = document.getElementById("game"),
      falcon = document.getElementById("falcon"),
      falconHeight = falcon.getBoundingClientRect().height,
      falconWidth = falcon.getBoundingClientRect().width,
      gameWindowSize = gameWindow.getBoundingClientRect(),
      gameWindowHeight = gameWindowSize.height,
      gameWindowWidth = gameWindowSize.width,
      gameWindowLeft = gameWindowSize.left,
      gameWindowTop = gameWindowSize.top,
      splashScreen = document.getElementById('splash'),
      splashTitle = document.getElementById('splashTitle'),
      startGameBtn = document.getElementById("start-game");


  // Responsive / resize
  function calculateGameDimensions() {
    var gameWindowSize = gameWindow.getBoundingClientRect();    
    gameWindowLeft = gameWindowSize.left;
    gameWindowTop = gameWindowSize.top;
    gameWindowWidth = gameWindowSize.width;
    gameWindowHeight = gameWindowSize.height;    
  }

  window.addEventListener("resize", calculateGameDimensions);  
  
  // Master game switch
  var gameIsRunning = false;

  
  // Falcon
  function falconBoom() {
    disableFalconControls();
    gsap.killTweensOf(falcon);
    gsap.set(falcon, {
      backgroundPositionX: '100%',
    });
    gsap.to(falcon, {
      autoAlpha: 0,
      scale: 1.2,
      duration: 2,
      ease: "power1.in",
    })
  }

  gsap.set(falcon, {xPercent: -50, yPercent: -50});

  var enterFalconDelay = 0.5;
  
  function enterFalcon(e) {
    gsap.killTweensOf(falcon);
    gsap.set(falcon, {
      autoAlpha: 1,
      backgroundPositionX: 0,
      scale: 1,
    });
    gsap.fromTo(falcon, {
      y: gameWindowHeight + falconHeight
    }, {
      y: gameWindowHeight - falconHeight - 16,
      duration: enterFalconDelay,
      ease: "power2.inOut",
      onComplete: enableFalconControls
    })
  }

  function moveFalcon(e) {
    if (gameIsRunning) {
      var falconPositionX = e.clientX - gameWindowLeft,
          falconPositionY = e.clientY - gameWindowTop;          
      gsap.to(falcon, {x: falconPositionX, duration: 0.16, y: falconPositionY, ease: "power1.out"});
    }
  }

  function tiltFalcon(e) {
    if (gameIsRunning) {
      // var x = -e.accelerationIncludingGravity.x * 2.5;
      var x = e.accelerationIncludingGravity.x * 1.33;
      var y = (e.accelerationIncludingGravity.y - 2) * 1.25;
      x = oneDecimal(x);  y = oneDecimal(y);
      var windowWidth = window.innerWidth;
      var windowHeight = window.innerHeight;

      var position = {
        'x': windowWidth  / (100 / toPercentage(x, 1)),
        'y': windowHeight / (100 / toPercentage(y, 1))
      }
      gsap.to('#falcon', {
        x: position.x,
        y: position.y,
        ease: "power1.out"
      });
    }
  }


  function enableFalconControls(e) {

    if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
      window.addEventListener("deviceorientation", tiltFalcon, true);
    } else if (navigator.standalone || window.matchMedia('(display-mode: standalone)').matches) {
      window.addEventListener("deviceorientation", tiltFalcon, true);
    } else {
      gameWindow.addEventListener("mousemove", moveFalcon);
    }

    gameWindow.addEventListener("click", fireLaser);
    gameWindow.addEventListener("contextmenu", fireLaser);
    gsap.delayedCall(1, enterObjects);
  }

  function disableFalconControls(e) {
    if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
      window.removeEventListener("devicemotion", tiltFalcon, true);
    } else if (navigator.standalone || window.matchMedia('(display-mode: standalone)').matches) {
      window.removeEventListener("deviceorientation", tiltFalcon, true);
    } else {
      gameWindow.removeEventListener("mousemove", moveFalcon);
    }
  }




  function enterObjects() {
    createObjects();
    startObjectAnim();
  }

  // Pew pew
  var laserCount = document.getElementById("laserCount"),
      lasers = 5,
      laserTickRate = 1000,
      ammo = lasers,
      increaseLaserCount = null;
  
  function updateLaserCount() {
    laserCount.textContent = ammo;
  }

  updateLaserCount();
  

  var laserShot = new Howl({
    src: ['laser.mp3']
  });

  function fireLaser(e) {
    e.preventDefault();    
    if (gameIsRunning) {
      if (ammo !== 0) {
        clearRafInterval(increaseLaserCount);
        increaseLaserCount = setRafInterval(addAmmo, laserTickRate);
        
        var falconPosition = falcon.getBoundingClientRect();

        var laserPositionX = falconPosition.left - gameWindowLeft + (falconWidth / 2),
            laserPositionY = falconPosition.top - gameWindowTop + (falconHeight / 1.5),
            laserDiv = document.createElement('div'),
            laserSpeed = laserPositionY / Math.pow(gameWindowHeight, 1.25);  

        gsap.set(laserDiv, {
          attr: {class: `laser`},
          x: laserPositionX,
          y: laserPositionY,
          yPercent: -50,
          xPercent: -50
        })
        gsap.to(laserDiv, {
          y: -40,
          duration: laserSpeed.toFixed(3),
          ease: "none",
          onComplete: removeLaser,
          onCompleteParams: [laserDiv],          
        });
        gameWindow.appendChild(laserDiv);
        laserShot.play();
        vibration(10);
      }      
      if (ammo <= lasers && ammo > 0) {
        ammo--;
        updateLaserCount();
      }
    }
  }

  function addAmmo() {
    if (ammo < lasers) {
      ammo++;
      updateLaserCount();      
    } else if (ammo === lasers) {
      clearRafInterval(increaseLaserCount);
    }
  }

  function removeLaser(element) {
    element.remove();
  }  

  


  // Objects
  var objects = 12,
      objectRate = null,
      objectStartingSpeed = 520,
      objectSpeed = objectStartingSpeed,
      objectEndingSpeed = 175,
      objectTickRate = 500,
      objectTypes = [
        "tie",
        "tie-bomber",
        "asteroid-sm",
        "asteroid-md",
        "asteroid-lg",
      ];
  

  function objectRateCounter() {
    objectSpeed--;
    if (objectSpeed <= objectEndingSpeed) {
      clearRafInterval(objectRate);
    }
  }
  

  function createObjects() {
    for (var i = 0; i < objects; i++) {
      var objectDiv = document.createElement('div');      
      objectDiv.className = `object object--${random(objectTypes)}`;
      gameWindow.appendChild(objectDiv);
      setNewObject(objectDiv);
    }
  }

  function setNewObject(element){
    var objectDim = element.getBoundingClientRect();
    gsap.set(element, {
      x: gsap.utils.random(objectDim.width, gameWindowWidth - objectDim.width),
      y: 0 - objectDim.height,
      yPercent: -50,    
      autoAlpha: 0,
    });
  }

  function startObjectAnim() {
    for (var i = 0; i < objects; i++) {
      var objectDiv = document.getElementsByClassName("object")[i];
      fallingObject(objectDiv, i);
    }
  }

  function replenishObjects() {
    var objectDivs = document.querySelectorAll('.object');
    if (objectDivs.length < objects) {      
      var objectDiv = document.createElement('div');      
      objectDiv.className = `object object--${random(objectTypes)}`;
      gameWindow.appendChild(objectDiv);
      setNewObject(objectDiv);
      fallingObject(objectDiv, gsap.utils.random(1,2));
    }    
  }  

  function fallingObject(element, delay) {
    var objectTL = new gsap.timeline(),
        objectDim = element.getBoundingClientRect(),
        duration = objectSpeed / 200;
        objectTL
        .set(element, {autoAlpha: 1})
        .to(element, {
          y: gameWindowHeight + objectDim.height,
          ease: "none",
          onUpdate: checkHits,
          onUpdateParams: [element],
          onComplete: () => {
            element.remove();
            replenishObjects();
          },
          duration: gsap.utils.random(duration, duration * 1.2),
          delay: delay * 1.5,
        });
  }
  
  var falconExplodeShot = new Howl({
    src: ['falcon-explode.mp3'],
    volume: 0.8,
  });
  var objectExplodeShot = new Howl({
    src: ['object-explode-1.mp3'],
    volume: 0.15,
  });

  function checkHits(element) {    
    var selectObject = element,
        laser = document.querySelectorAll('.laser');
    if (laser.length) {
      if (Draggable.hitTest(laser, selectObject, "0%")) {
        selectObject.remove();
        objectExplodeShot.play();
        replenishObjects();
        addHitToScore();
      }
    }
    if (Draggable.hitTest(falcon, selectObject, "15%")) {
      falconExplodeShot.play();
      gameOver();
    }
  }
  
  // Backgrounds
  function backgroundScroller() {
    var backgroundDuration = objectSpeed / 175;
    gsap.set(gameWindow, {backgroundPositionY: '0px'});
    gsap.to(gameWindow, {
      backgroundPositionY: `${gameWindowSize.height}px`,
      duration: backgroundDuration,
      ease: "none",
      onComplete: backgroundScroller
    })
  }

  // Game scores
  var gameProgress = 0,
      hitScore = 50,
      score = document.getElementById("score"),
      highScore = document.getElementById("highScore"),
      gameProgressTicker = null;  

  function highScoreCheck() {
    var highScoreStore = localStorage.getItem("falcon-madness-highscore");
    if (highScoreStore === null || gameProgress > highScoreStore) {
      localStorage.setItem("falcon-madness-highscore", gameProgress);
      updateScoreText(highScore, gameProgress);
    } else if (highScoreStore !== null) {
      highScore.textContent = highScoreStore;
    }
  }

  
  
  function gameProgressCounter() {
    gameProgress++;
    updateScoreText(score, gameProgress);    
  }

  function updateScoreText(el, num) {
    el.textContent = num.toFixed(0).toString();
  } 

  function addHitToScore() {
    gameProgress = gameProgress + hitScore;
  }

  updateScoreText(score, gameProgress); 
  highScoreCheck();
    

  function clearTimers() {
    clearRafInterval(increaseLaserCount);
    clearRafInterval(gameProgressTicker);
    clearRafInterval(objectRate);
  }

  function startGame() {
    gameIsRunning = true;
    gsap.set(gameWindow, {backgroundPositionY: 0});
    splashScreen.style.display = 'none';
    document.querySelectorAll('.object').forEach(e => e.remove());    
    gameProgress = 0;
    gameProgressTicker = setRafInterval(gameProgressCounter, 100);  
    objectSpeed = objectStartingSpeed;
    objectRate = setRafInterval(objectRateCounter, objectTickRate);  
    ammo = lasers;
    updateLaserCount();
    enterFalcon();
    gameProgressCounter();
    backgroundScroller();
    gameWindow.classList.add("game-is-running");
  }

  function gameOver() {
    gameIsRunning = false;    
    vibration(300);
    highScoreCheck();
    splashScreen.style.display = 'block';
    splashTitle.textContent = 'Game over';
    startGameBtn.textContent = 'Try again?';
    startGameBtn.style.display = 'block';    
    clearTimers();
    gsap.killTweensOf(".object");
    gsap.killTweensOf(gameWindow);    
    falconBoom();
    gameWindow.classList.remove("game-is-running");    
  }

  startGameBtn.addEventListener("click", startGame);

  

  // Rotation checks
  function rotateToggle() {
    var rotate = document.getElementById('rotation');
    var orientation = (screen.orientation || {}).type || screen.mozOrientation || screen.msOrientation;
    if (orientation === "landscape-primary") {
      rotate.style.display = "block";
    } else if (orientation === "portrait-secondary" || orientation === "portrait-primary") {
      rotate.style.display = "none";
    }
  }

  function rotateCheck() {       
    if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
      rotateToggle();
      window.addEventListener('orientationchange', rotateToggle);
    }
  }
  
  rotateCheck();

}

